<template>
  <div class="wrapper" ref="wrapper">
      <div class="content">
          <slot></slot>
      </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'

export default {
  props: {
    probeType:{
      type: Number,
      default: 0
    },
    pullupLoad:{
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
        scroll:null
    }
  },
  computed: {},
  mounted() {
    // 创建 Bscroll 对象
      this.scroll = new Bscroll(this.$refs.wrapper,{
          observeDOM: true,
          click: true,
          probeType: this.probeType,
          pullUpLoad: this.pullupLoad,
      })
    // 监听滚动的 位置  来显示隐藏 按钮
    this.scroll.on('scroll',(a)=>{
      this.$emit('scroll',a)
    })
    // 监听上拉事件
    this.scroll.on('pullingUp', ()=>{
     this.$emit('pullingUp')
    })
  },
  methods: {
    // 点击回到顶部
    scrollTo(x,y,time){
      this.scroll.scrollTo(x,y,time)
    },
    //   finishPullUp: 这个类似控制一个开关，比如在触发pullingUp事件的时候，插件肯定会把一个开关给关掉，防止用户重复上拉
    // 	在数据加载完成以后，需要执行finishPullUp()把开关打开，以便下次可以继续执行上拉刷新；
    finishPullUp(){
      this.scroll.finishPullUp()
    },
    //  refresh：其实就是重新计算一下内容的高度和宽度，也许也会计算外框的高度和宽度，因为dom结构发生了变化了，所以宽高要从新计算
    //  以为整个插件基本上都是在计算一些距离差值，所以肯定需要从新计算的；
    refresh(){
    this.scroll && this.scroll.refresh()
    console.log('图片加载完成，重新计算滚动高度');
    },
    // 记录滚动的Y的值
    getScrollY(){
      return this.scroll.y
    }
  },
  components: {
      Bscroll
  }
}
</script>

